'use client';

import { useState, useEffect } from 'react';

interface SavingsPot {
    id: number;
    name: string;
    goal_amount: number | null;
    color: string;
    icon: string;
}

interface ManagePotsProps {
    onUpdate: () => void;
}

export default function ManagePots({ onUpdate }: ManagePotsProps) {
    const [pots, setPots] = useState<SavingsPot[]>([]);
    const [showForm, setShowForm] = useState(false);
    const [formData, setFormData] = useState({
        name: '',
        goalAmount: '',
        color: '#059669',
        icon: 'piggy-bank'
    });

    useEffect(() => {
        fetchPots();
    }, []);

    const fetchPots = async () => {
        try {
            const res = await fetch('/api/pots');
            const data = await res.json();
            setPots(data.pots || []);
        } catch (error) {
            console.error('Error fetching pots:', error);
        }
    };

    const handleSubmit = async (e: React.FormEvent) => {
        e.preventDefault();

        try {
            const res = await fetch('/api/pots', {
                method: 'POST',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify({
                    name: formData.name,
                    goalAmount: formData.goalAmount ? parseFloat(formData.goalAmount) : null,
                    color: formData.color,
                    icon: formData.icon,
                    priority: 0
                })
            });

            if (!res.ok) throw new Error('Failed to create pot');

            setFormData({ name: '', goalAmount: '', color: '#059669', icon: 'piggy-bank' });
            setShowForm(false);
            fetchPots();
            onUpdate();
        } catch (error) {
            console.error('Error creating pot:', error);
            alert('Failed to create savings pot');
        }
    };

    const handleDelete = async (id: number) => {
        if (!confirm('Are you sure? This will delete all associated accounts and transactions.')) return;

        try {
            const res = await fetch(`/api/pots/${id}`, { method: 'DELETE' });
            if (!res.ok) throw new Error('Failed to delete pot');

            fetchPots();
            onUpdate();
        } catch (error) {
            console.error('Error deleting pot:', error);
            alert('Failed to delete pot');
        }
    };

    return (
        <div>
            <div className="flex justify-between items-center mb-lg">
                <h2>Savings Pots</h2>
                <button onClick={() => setShowForm(!showForm)} className="btn btn-primary">
                    {showForm ? 'Cancel' : '+ Add Pot'}
                </button>
            </div>

            {showForm && (
                <form onSubmit={handleSubmit} className="card mb-lg">
                    <div className="form-group">
                        <label className="form-label">Pot Name</label>
                        <input
                            type="text"
                            className="form-input"
                            value={formData.name}
                            onChange={(e) => setFormData({ ...formData, name: e.target.value })}
                            placeholder="e.g., House Deposit"
                            required
                        />
                    </div>

                    <div className="form-group">
                        <label className="form-label">Goal Amount (¬£, Optional)</label>
                        <input
                            type="number"
                            step="0.01"
                            className="form-input"
                            value={formData.goalAmount}
                            onChange={(e) => setFormData({ ...formData, goalAmount: e.target.value })}
                            placeholder="e.g., 50000"
                        />
                    </div>

                    <div className="form-group">
                        <label className="form-label">Icon</label>
                        <select
                            className="form-select"
                            value={formData.icon}
                            onChange={(e) => setFormData({ ...formData, icon: e.target.value })}
                        >
                            <option value="house">üè° House</option>
                            <option value="piggy-bank">üê∑ Piggy Bank</option>
                            <option value="car">üöó Car</option>
                            <option value="vacation">üèñÔ∏è Vacation</option>
                            <option value="emergency">üö® Emergency</option>
                            <option value="wedding">üíç Wedding</option>
                            <option value="education">üéì Education</option>
                            <option value="savings">üí∞ Savings</option>
                        </select>
                    </div>

                    <div className="form-group">
                        <label className="form-label">Color</label>
                        <input
                            type="color"
                            className="form-input"
                            value={formData.color}
                            onChange={(e) => setFormData({ ...formData, color: e.target.value })}
                        />
                    </div>

                    <button type="submit" className="btn btn-primary" style={{ width: '100%' }}>
                        Create Pot
                    </button>
                </form>
            )}

            <div className="grid" style={{ gap: 'var(--spacing-md)' }}>
                {pots.map((pot) => (
                    <div key={pot.id} className="card">
                        <div className="flex justify-between items-center">
                            <div className="flex items-center gap-md">
                                <div style={{ fontSize: '2rem' }}>
                                    {pot.icon === 'house' && 'üè°'}
                                    {pot.icon === 'piggy-bank' && 'üê∑'}
                                    {pot.icon === 'car' && 'üöó'}
                                    {pot.icon === 'vacation' && 'üèñÔ∏è'}
                                    {pot.icon === 'emergency' && 'üö®'}
                                    {pot.icon === 'wedding' && 'üíç'}
                                    {pot.icon === 'education' && 'üéì'}
                                    {pot.icon === 'savings' && 'üí∞'}
                                </div>
                                <div>
                                    <h4 style={{ marginBottom: '0.25rem' }}>{pot.name}</h4>
                                    {pot.goal_amount && (
                                        <p className="text-muted" style={{ fontSize: '0.875rem', margin: 0 }}>
                                            Goal: ¬£{pot.goal_amount.toLocaleString('en-GB')}
                                        </p>
                                    )}
                                </div>
                            </div>
                            <button
                                onClick={() => handleDelete(pot.id)}
                                className="btn btn-secondary"
                                style={{ padding: '0.5rem' }}
                            >
                                üóëÔ∏è
                            </button>
                        </div>
                    </div>
                ))}
            </div>
        </div>
    );
}
