'use client';

import { useState, useEffect } from 'react';

interface Account {
    id: number;
    pot_id: number;
    pot_name: string;
    account_name: string;
    account_type: string;
    current_balance: number;
}

interface SavingsPot {
    id: number;
    name: string;
}

interface ManageAccountsProps {
    onUpdate: () => void;
}

export default function ManageAccounts({ onUpdate }: ManageAccountsProps) {
    const [accounts, setAccounts] = useState<Account[]>([]);
    const [pots, setPots] = useState<SavingsPot[]>([]);
    const [showForm, setShowForm] = useState(false);
    const [formData, setFormData] = useState({
        potId: '',
        accountName: '',
        accountType: 'savings',
        currentBalance: ''
    });

    useEffect(() => {
        fetchData();
    }, []);

    const fetchData = async () => {
        try {
            const [accountsRes, potsRes] = await Promise.all([
                fetch('/api/accounts'),
                fetch('/api/pots')
            ]);
            const accountsData = await accountsRes.json();
            const potsData = await potsRes.json();
            setAccounts(accountsData.accounts || []);
            setPots(potsData.pots || []);
        } catch (error) {
            console.error('Error fetching data:', error);
        }
    };

    const handleSubmit = async (e: React.FormEvent) => {
        e.preventDefault();

        try {
            const res = await fetch('/api/accounts', {
                method: 'POST',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify({
                    potId: parseInt(formData.potId),
                    accountName: formData.accountName,
                    accountType: formData.accountType,
                    currentBalance: parseFloat(formData.currentBalance) || 0
                })
            });

            if (!res.ok) throw new Error('Failed to create account');

            setFormData({ potId: '', accountName: '', accountType: 'savings', currentBalance: '' });
            setShowForm(false);
            fetchData();
            onUpdate();
        } catch (error) {
            console.error('Error creating account:', error);
            alert('Failed to create account');
        }
    };

    const handleDelete = async (id: number) => {
        if (!confirm('Are you sure? This will delete all associated transactions.')) return;

        try {
            const res = await fetch(`/api/accounts/${id}`, { method: 'DELETE' });
            if (!res.ok) throw new Error('Failed to delete account');

            fetchData();
            onUpdate();
        } catch (error) {
            console.error('Error deleting account:', error);
            alert('Failed to delete account');
        }
    };

    return (
        <div>
            <div className="flex justify-between items-center mb-lg">
                <h2>Accounts</h2>
                <button onClick={() => setShowForm(!showForm)} className="btn btn-primary">
                    {showForm ? 'Cancel' : '+ Add Account'}
                </button>
            </div>

            {showForm && (
                <form onSubmit={handleSubmit} className="card mb-lg">
                    <div className="form-group">
                        <label className="form-label">Savings Pot</label>
                        <select
                            className="form-select"
                            value={formData.potId}
                            onChange={(e) => setFormData({ ...formData, potId: e.target.value })}
                            required
                        >
                            <option value="">Select a pot</option>
                            {pots.map((pot) => (
                                <option key={pot.id} value={pot.id}>
                                    {pot.name}
                                </option>
                            ))}
                        </select>
                    </div>

                    <div className="form-group">
                        <label className="form-label">Account Name</label>
                        <input
                            type="text"
                            className="form-input"
                            value={formData.accountName}
                            onChange={(e) => setFormData({ ...formData, accountName: e.target.value })}
                            placeholder="e.g., HSBC ISA"
                            required
                        />
                    </div>

                    <div className="form-group">
                        <label className="form-label">Account Type</label>
                        <select
                            className="form-select"
                            value={formData.accountType}
                            onChange={(e) => setFormData({ ...formData, accountType: e.target.value })}
                        >
                            <option value="savings">Savings Account</option>
                            <option value="isa">ISA</option>
                            <option value="current">Current Account</option>
                            <option value="investment">Investment Account</option>
                            <option value="other">Other</option>
                        </select>
                    </div>

                    <div className="form-group">
                        <label className="form-label">Current Balance (¬£)</label>
                        <input
                            type="number"
                            step="0.01"
                            className="form-input"
                            value={formData.currentBalance}
                            onChange={(e) => setFormData({ ...formData, currentBalance: e.target.value })}
                            placeholder="0.00"
                        />
                    </div>

                    <button type="submit" className="btn btn-primary" style={{ width: '100%' }}>
                        Create Account
                    </button>
                </form>
            )}

            <div className="grid" style={{ gap: 'var(--spacing-md)' }}>
                {accounts.map((account) => (
                    <div key={account.id} className="card">
                        <div className="flex justify-between items-center">
                            <div>
                                <h4 style={{ marginBottom: '0.25rem' }}>{account.account_name}</h4>
                                <p className="text-muted" style={{ fontSize: '0.875rem', margin: '0 0 0.5rem 0' }}>
                                    {account.pot_name} ‚Ä¢ {account.account_type}
                                </p>
                                <p className="currency" style={{ fontSize: '1.25rem', fontWeight: 600, margin: 0 }}>
                                    ¬£{account.current_balance.toLocaleString('en-GB', { minimumFractionDigits: 2 })}
                                </p>
                            </div>
                            <button
                                onClick={() => handleDelete(account.id)}
                                className="btn btn-secondary"
                                style={{ padding: '0.5rem' }}
                            >
                                üóëÔ∏è
                            </button>
                        </div>
                    </div>
                ))}
            </div>
        </div>
    );
}
