'use client';

import { useState, useEffect } from 'react';
import Dashboard from '@/components/Dashboard';
import Login from '@/components/Login';
import SpreadsheetView from '@/components/SpreadsheetView';
import TransactionForm from '@/components/TransactionForm';
import ManagePots from '@/components/ManagePots';
import ManageAccounts from '@/components/ManageAccounts';

export default function Home() {
  const [user, setUser] = useState<any>(null);
  const [currentView, setCurrentView] = useState<'dashboard' | 'transactions' | 'manage'>('dashboard');
  const [loading, setLoading] = useState(true);
  const [showTransactionForm, setShowTransactionForm] = useState(false);
  const [refreshKey, setRefreshKey] = useState(0);

  useEffect(() => {
    checkSession();
  }, []);

  const checkSession = async () => {
    try {
      const res = await fetch('/api/auth/session');
      if (res.ok) {
        const data = await res.json();
        if (data.authenticated) {
          setUser(data.user);
        }
      }
    } catch (error) {
      console.error('Session check error:', error);
    } finally {
      setLoading(false);
    }
  };

  const handleLogout = async () => {
    await fetch('/api/auth/session', { method: 'POST' });
    setUser(null);
  };

  const handleTransactionSuccess = () => {
    setShowTransactionForm(false);
    setRefreshKey((prev) => prev + 1);
  };

  if (loading) {
    return (
      <div className="page" style={{ display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
        <div className="skeleton" style={{ width: '300px', height: '400px' }} />
      </div>
    );
  }

  if (!user) {
    return <Login onLogin={setUser} />;
  }

  return (
    <div className="page">
      {/* Header */}
      <div className="container">
        <header style={{ padding: 'var(--spacing-lg) 0', borderBottom: '2px solid var(--border)', marginBottom: 'var(--spacing-xl)' }}>
          <div className="flex justify-between items-center">
            <div>
              <h1 style={{ marginBottom: 'var(--spacing-xs)' }}>üí∞ Savings Tracker</h1>
              <p className="text-muted">Welcome back, {user.displayName}!</p>
            </div>
            <button onClick={handleLogout} className="btn btn-secondary">
              Logout
            </button>
          </div>
        </header>

        {/* Navigation */}
        <nav className="mb-xl">
          <div className="flex gap-md" style={{ overflowX: 'auto' }}>
            <button
              onClick={() => setCurrentView('dashboard')}
              className={currentView === 'dashboard' ? 'btn btn-primary' : 'btn btn-secondary'}
            >
              üìä Dashboard
            </button>
            <button
              onClick={() => setCurrentView('transactions')}
              className={currentView === 'transactions' ? 'btn btn-primary' : 'btn btn-secondary'}
            >
              üìã Transactions
            </button>
            <button
              onClick={() => setCurrentView('manage')}
              className={currentView === 'manage' ? 'btn btn-primary' : 'btn btn-secondary'}
            >
              ‚öôÔ∏è Manage
            </button>
          </div>
        </nav>

        {/* Main content */}
        <main>
          {currentView === 'dashboard' && <Dashboard key={refreshKey} />}
          {currentView === 'transactions' && <SpreadsheetView key={refreshKey} />}
          {currentView === 'manage' && (
            <div className="grid grid-2">
              <ManagePots onUpdate={() => setRefreshKey((prev) => prev + 1)} />
              <ManageAccounts onUpdate={() => setRefreshKey((prev) => prev + 1)} />
            </div>
          )}
        </main>
      </div>

      {/* Floating Action Button */}
      {!showTransactionForm && (
        <button
          className="fab"
          onClick={() => setShowTransactionForm(true)}
          title="Add Transaction"
        >
          +
        </button>
      )}

      {/* Transaction Form Modal */}
      {showTransactionForm && (
        <div
          style={{
            position: 'fixed',
            top: 0,
            left: 0,
            right: 0,
            bottom: 0,
            background: 'rgba(0, 0, 0, 0.5)',
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            padding: 'var(--spacing-md)',
            zIndex: 1000,
            overflowY: 'auto'
          }}
          onClick={(e) => {
            if (e.target === e.currentTarget) setShowTransactionForm(false);
          }}
        >
          <div style={{ maxWidth: '500px', width: '100%', position: 'relative' }}>
            <button
              onClick={() => setShowTransactionForm(false)}
              style={{
                position: 'absolute',
                top: '-12px',
                right: '-12px',
                width: '40px',
                height: '40px',
                borderRadius: 'var(--radius-full)',
                background: 'var(--error)',
                color: 'white',
                border: 'none',
                fontSize: '1.5rem',
                cursor: 'pointer',
                zIndex: 1001
              }}
            >
              √ó
            </button>
            <TransactionForm onSuccess={handleTransactionSuccess} currentUser={user} />
          </div>
        </div>
      )}
    </div>
  );
}
