'use client';

import { useState, useEffect } from 'react';

interface SavingsPot {
    id: number;
    name: string;
    goal_amount: number | null;
    color: string;
    icon: string;
    total_balance: number;
}

export default function Dashboard() {
    const [pots, setPots] = useState<SavingsPot[]>([]);
    const [loading, setLoading] = useState(true);

    useEffect(() => {
        fetchPots();
    }, []);

    const fetchPots = async () => {
        try {
            const res = await fetch('/api/pots');
            const data = await res.json();
            setPots(data.pots || []);
        } catch (error) {
            console.error('Error fetching pots:', error);
        } finally {
            setLoading(false);
        }
    };

    const totalSavings = pots.reduce((sum, pot) => sum + (pot.total_balance || 0), 0);

    if (loading) {
        return (
            <div className="grid grid-2 mt-lg">
                {[1, 2, 3].map((i) => (
                    <div key={i} className="skeleton" style={{ height: '200px' }} />
                ))}
            </div>
        );
    }

    return (
        <div>
            {/* Total overview */}
            <div className="card mb-xl" style={{ background: 'linear-gradient(135deg, #059669 0%, #047857 100%)', color: 'white' }}>
                <p style={{ fontSize: '0.875rem', opacity: 0.9, marginBottom: '0.5rem' }}>Total Savings</p>
                <h1 className="currency currency-large mb-0">
                    ¬£{totalSavings.toLocaleString('en-GB', { minimumFractionDigits: 2, maximumFractionDigits: 2 })}
                </h1>
            </div>

            {/* Savings pots grid */}
            {pots.length === 0 ? (
                <div className="card text-center">
                    <p className="text-muted">No savings pots yet. Create one to get started!</p>
                </div>
            ) : (
                <div className="grid grid-2">
                    {pots.map((pot) => (
                        <PotCard key={pot.id} pot={pot} />
                    ))}
                </div>
            )}
        </div>
    );
}

function PotCard({ pot }: { pot: SavingsPot }) {
    const progress = pot.goal_amount ? (pot.total_balance / pot.goal_amount) * 100 : 0;
    const isGoalMet = pot.goal_amount && pot.total_balance >= pot.goal_amount;

    return (
        <a href={`/pot/${pot.id}`} className="card" style={{ textDecoration: 'none', color: 'inherit' }}>
            <div className="flex items-center justify-between mb-md">
                <div>
                    <h3 className="card-title mb-0">{pot.name}</h3>
                    {pot.goal_amount && (
                        <p className="text-muted" style={{ fontSize: '0.875rem' }}>
                            Goal: ¬£{pot.goal_amount.toLocaleString('en-GB')}
                        </p>
                    )}
                </div>
                <div
                    style={{
                        width: '48px',
                        height: '48px',
                        borderRadius: 'var(--radius-md)',
                        background: pot.color + '20',
                        color: pot.color,
                        display: 'flex',
                        alignItems: 'center',
                        justifyContent: 'center',
                        fontSize: '1.5rem'
                    }}
                >
                    {getIcon(pot.icon)}
                </div>
            </div>

            <div className="mb-md">
                <div className="flex justify-between mb-sm">
                    <span className="currency currency-medium" style={{ color: pot.color }}>
                        ¬£{pot.total_balance.toLocaleString('en-GB', { minimumFractionDigits: 2, maximumFractionDigits: 2 })}
                    </span>
                    {pot.goal_amount && (
                        <span className="text-muted" style={{ fontSize: '0.875rem', fontWeight: 600 }}>
                            {Math.min(progress, 100).toFixed(0)}%
                        </span>
                    )}
                </div>

                {pot.goal_amount && (
                    <div className="progress-container">
                        <div
                            className="progress-bar"
                            style={{
                                width: `${Math.min(progress, 100)}%`,
                                background: `linear-gradient(90deg, ${pot.color} 0%, ${pot.color}dd 100%)`
                            }}
                        />
                    </div>
                )}
            </div>

            {isGoalMet && (
                <div
                    style={{
                        padding: '0.5rem',
                        borderRadius: 'var(--radius-sm)',
                        background: 'var(--success)',
                        color: 'white',
                        fontSize: '0.875rem',
                        fontWeight: 600,
                        textAlign: 'center'
                    }}
                >
                    üéâ Goal Reached!
                </div>
            )}
        </a>
    );
}

function getIcon(icon: string) {
    const icons: Record<string, string> = {
        'piggy-bank': 'üê∑',
        'house': 'üè°',
        'car': 'üöó',
        'vacation': 'üèñÔ∏è',
        'emergency': 'üö®',
        'wedding': 'üíç',
        'education': 'üéì',
        'savings': 'üí∞'
    };
    return icons[icon] || 'üí∞';
}
