'use client';

import { useState, useEffect } from 'react';

interface Account {
    id: number;
    pot_id: number;
    account_name: string;
    account_type: string;
    current_balance: number;
}

interface SavingsPot {
    id: number;
    name: string;
}

interface TransactionFormProps {
    onSuccess: () => void;
    currentUser: { id: number; username: string; displayName: string };
}

export default function TransactionForm({ onSuccess, currentUser }: TransactionFormProps) {
    const [accounts, setAccounts] = useState<Account[]>([]);
    const [pots, setPots] = useState<SavingsPot[]>([]);
    const [selectedAccountId, setSelectedAccountId] = useState('');
    const [amount, setAmount] = useState('');
    const [description, setDescription] = useState('');
    const [transactionDate, setTransactionDate] = useState(new Date().toISOString().split('T')[0]);
    const [isDeposit, setIsDeposit] = useState(true);
    const [loading, setLoading] = useState(false);

    useEffect(() => {
        fetchData();
    }, []);

    const fetchData = async () => {
        try {
            const [accountsRes, potsRes] = await Promise.all([
                fetch('/api/accounts'),
                fetch('/api/pots')
            ]);
            const accountsData = await accountsRes.json();
            const potsData = await potsRes.json();
            setAccounts(accountsData.accounts || []);
            setPots(potsData.pots || []);
        } catch (error) {
            console.error('Error fetching data:', error);
        }
    };

    const handleSubmit = async (e: React.FormEvent) => {
        e.preventDefault();
        setLoading(true);

        try {
            const finalAmount = isDeposit ? parseFloat(amount) : -parseFloat(amount);

            const res = await fetch('/api/transactions', {
                method: 'POST',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify({
                    accountId: parseInt(selectedAccountId),
                    userId: currentUser.id,
                    amount: finalAmount,
                    description,
                    transactionDate
                })
            });

            if (!res.ok) throw new Error('Failed to create transaction');

            // Reset form
            setAmount('');
            setDescription('');
            setTransactionDate(new Date().toISOString().split('T')[0]);
            onSuccess();
        } catch (error) {
            console.error('Error creating transaction:', error);
            alert('Failed to create transaction');
        } finally {
            setLoading(false);
        }
    };

    return (
        <form onSubmit={handleSubmit} className="card">
            <h2 className="mb-lg">Add Transaction</h2>

            <div className="form-group">
                <label className="form-label" htmlFor="account">Account</label>
                <select
                    id="account"
                    className="form-select"
                    value={selectedAccountId}
                    onChange={(e) => setSelectedAccountId(e.target.value)}
                    required
                >
                    <option value="">Select an account</option>
                    {pots.map((pot) => {
                        const potAccounts = accounts.filter((a) => a.pot_id === pot.id);
                        return potAccounts.length > 0 ? (
                            <optgroup key={pot.id} label={pot.name}>
                                {potAccounts.map((account) => (
                                    <option key={account.id} value={account.id}>
                                        {account.account_name} (¬£{account.current_balance.toLocaleString('en-GB')})
                                    </option>
                                ))}
                            </optgroup>
                        ) : null;
                    })}
                </select>
            </div>

            <div className="form-group">
                <label className="form-label">Transaction Type</label>
                <div className="flex gap-md">
                    <button
                        type="button"
                        className={isDeposit ? 'btn btn-primary' : 'btn btn-secondary'}
                        onClick={() => setIsDeposit(true)}
                        style={{ flex: 1 }}
                    >
                        üí∞ Deposit
                    </button>
                    <button
                        type="button"
                        className={!isDeposit ? 'btn btn-primary' : 'btn btn-secondary'}
                        onClick={() => setIsDeposit(false)}
                        style={{ flex: 1 }}
                    >
                        üí∏ Withdrawal
                    </button>
                </div>
            </div>

            <div className="form-group">
                <label className="form-label" htmlFor="amount">Amount (¬£)</label>
                <input
                    id="amount"
                    type="number"
                    step="0.01"
                    min="0.01"
                    className="form-input"
                    value={amount}
                    onChange={(e) => setAmount(e.target.value)}
                    placeholder="0.00"
                    required
                />
            </div>

            <div className="form-group">
                <label className="form-label" htmlFor="date">Date</label>
                <input
                    id="date"
                    type="date"
                    className="form-input"
                    value={transactionDate}
                    onChange={(e) => setTransactionDate(e.target.value)}
                    required
                />
            </div>

            <div className="form-group">
                <label className="form-label" htmlFor="description">Description (Optional)</label>
                <input
                    id="description"
                    type="text"
                    className="form-input"
                    value={description}
                    onChange={(e) => setDescription(e.target.value)}
                    placeholder="e.g., Monthly savings"
                />
            </div>

            <button type="submit" className="btn btn-primary" style={{ width: '100%' }} disabled={loading}>
                {loading ? 'Adding...' : `Add ${isDeposit ? 'Deposit' : 'Withdrawal'}`}
            </button>
        </form>
    );
}
