'use client';

import { useState, useEffect } from 'react';

interface Transaction {
    id: number;
    account_name: string;
    pot_name: string;
    user_name: string;
    amount: number;
    description: string;
    transaction_date: string;
}

export default function SpreadsheetView() {
    const [transactions, setTransactions] = useState<Transaction[]>([]);
    const [loading, setLoading] = useState(true);

    useEffect(() => {
        fetchTransactions();
    }, []);

    const fetchTransactions = async () => {
        try {
            const res = await fetch('/api/transactions');
            const data = await res.json();
            setTransactions(data.transactions || []);
        } catch (error) {
            console.error('Error fetching transactions:', error);
        } finally {
            setLoading(false);
        }
    };

    const handleExport = async () => {
        try {
            const res = await fetch('/api/export/csv');
            const blob = await res.blob();
            const url = window.URL.createObjectURL(blob);
            const a = document.createElement('a');
            a.href = url;
            a.download = `savings-tracker-${new Date().toISOString().split('T')[0]}.csv`;
            document.body.appendChild(a);
            a.click();
            window.URL.revokeObjectURL(url);
            document.body.removeChild(a);
        } catch (error) {
            console.error('Export error:', error);
            alert('Failed to export data');
        }
    };

    if (loading) {
        return <div className="skeleton" style={{ height: '400px' }} />;
    }

    return (
        <div>
            <div className="flex justify-between items-center mb-lg">
                <h2>Transaction History</h2>
                <button onClick={handleExport} className="btn btn-primary">
                    üìä Export to CSV
                </button>
            </div>

            <div className="card" style={{ overflowX: 'auto' }}>
                {transactions.length === 0 ? (
                    <p className="text-muted text-center">No transactions yet</p>
                ) : (
                    <table style={{ width: '100%', borderCollapse: 'collapse' }}>
                        <thead>
                            <tr style={{ borderBottom: '2px solid var(--border)', textAlign: 'left' }}>
                                <th style={{ padding: 'var(--spacing-md)', whiteSpace: 'nowrap' }}>Date</th>
                                <th style={{ padding: 'var(--spacing-md)', whiteSpace: 'nowrap' }}>Pot</th>
                                <th style={{ padding: 'var(--spacing-md)', whiteSpace: 'nowrap' }}>Account</th>
                                <th style={{ padding: 'var(--spacing-md)', whiteSpace: 'nowrap' }}>Amount</th>
                                <th style={{ padding: 'var(--spacing-md)', whiteSpace: 'nowrap' }}>Description</th>
                                <th style={{ padding: 'var(--spacing-md)', whiteSpace: 'nowrap' }}>User</th>
                            </tr>
                        </thead>
                        <tbody>
                            {transactions.map((tx) => (
                                <tr key={tx.id} style={{ borderBottom: '1px solid var(--border)' }}>
                                    <td style={{ padding: 'var(--spacing-md)', whiteSpace: 'nowrap' }}>
                                        {new Date(tx.transaction_date).toLocaleDateString('en-GB')}
                                    </td>
                                    <td style={{ padding: 'var(--spacing-md)', whiteSpace: 'nowrap' }}>{tx.pot_name}</td>
                                    <td style={{ padding: 'var(--spacing-md)', whiteSpace: 'nowrap' }}>{tx.account_name}</td>
                                    <td style={{ padding: 'var(--spacing-md)', whiteSpace: 'nowrap' }} className="currency">
                                        <span style={{ color: tx.amount >= 0 ? 'var(--success)' : 'var(--error)' }}>
                                            {tx.amount >= 0 ? '+' : ''}¬£{Math.abs(tx.amount).toLocaleString('en-GB', { minimumFractionDigits: 2 })}
                                        </span>
                                    </td>
                                    <td style={{ padding: 'var(--spacing-md)' }}>{tx.description || '-'}</td>
                                    <td style={{ padding: 'var(--spacing-md)', whiteSpace: 'nowrap' }}>{tx.user_name}</td>
                                </tr>
                            ))}
                        </tbody>
                    </table>
                )}
            </div>
        </div>
    );
}
