'use client';

import { useState } from 'react';

interface LoginProps {
    onLogin: (user: any) => void;
}

export default function Login({ onLogin }: LoginProps) {
    const [username, setUsername] = useState('');
    const [password, setPassword] = useState('');
    const [error, setError] = useState('');
    const [loading, setLoading] = useState(false);

    const handleSubmit = async (e: React.FormEvent) => {
        e.preventDefault();
        setError('');
        setLoading(true);

        try {
            const res = await fetch('/api/auth/login', {
                method: 'POST',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify({ username, password })
            });

            const data = await res.json();

            if (!res.ok) {
                setError(data.error || 'Login failed');
                return;
            }

            onLogin(data.user);
        } catch (err) {
            setError('An error occurred. Please try again.');
        } finally {
            setLoading(false);
        }
    };

    return (
        <div className="page" style={{ display: 'flex', alignItems: 'center', justifyContent: 'center', background: 'linear-gradient(135deg, #059669 0%, #047857 100%)' }}>
            <div className="card" style={{ width: '100%', maxWidth: '400px', margin: 'var(--spacing-md)' }}>
                <div className="text-center mb-xl">
                    <div style={{ fontSize: '3rem', marginBottom: 'var(--spacing-md)' }}>üè°</div>
                    <h1 style={{ marginBottom: 'var(--spacing-sm)' }}>Savings Tracker</h1>
                    <p className="text-muted">Track your journey to your first home</p>
                </div>

                <form onSubmit={handleSubmit}>
                    <div className="form-group">
                        <label className="form-label" htmlFor="username">Username</label>
                        <input
                            id="username"
                            type="text"
                            className="form-input"
                            value={username}
                            onChange={(e) => setUsername(e.target.value)}
                            placeholder="user1 or user2"
                            required
                        />
                    </div>

                    <div className="form-group">
                        <label className="form-label" htmlFor="password">Password</label>
                        <input
                            id="password"
                            type="password"
                            className="form-input"
                            value={password}
                            onChange={(e) => setPassword(e.target.value)}
                            placeholder="Enter password"
                            required
                        />
                    </div>

                    {error && (
                        <div style={{ padding: 'var(--spacing-md)', background: '#fef2f2', border: '2px solid var(--error)', borderRadius: 'var(--radius-md)', color: 'var(--error)', marginBottom: 'var(--spacing-md)' }}>
                            {error}
                        </div>
                    )}

                    <button type="submit" className="btn btn-primary" style={{ width: '100%' }} disabled={loading}>
                        {loading ? 'Logging in...' : 'Login'}
                    </button>

                    <div style={{ marginTop: 'var(--spacing-lg)', padding: 'var(--spacing-md)', background: 'var(--background)', borderRadius: 'var(--radius-md)', fontSize: '0.875rem' }}>
                        <p style={{ margin: 0 }}><strong>Default credentials:</strong></p>
                        <p style={{ margin: '0.5rem 0 0 0' }}>user1 / changeme123<br />user2 / changeme123</p>
                    </div>
                </form>
            </div>
        </div>
    );
}
